import { getBookings } from '@/lib/db'

export default async function BookingPage({ params }: { params: { id: string } }) {
  const id = parseInt(params.id, 10)
  const bookings = await getBookings()
  const booking = bookings.find(b => b.id === id)

  if (!booking) {
    return (
      <div className="p-6">
        <h2 className="text-xl font-semibold">Booking not found</h2>
        <p className="mt-2 text-sm">We couldn't find a booking with ID {id}.</p>
      </div>
    )
  }

  return (
    <div className="p-6">
      <h2 className="text-xl font-semibold">Booking Confirmed</h2>
      <div className="mt-4 space-y-2">
        <div><strong>Booking ID:</strong> {booking.id}</div>
        <div><strong>Reference:</strong> {booking.booking_reference}</div>
        <div><strong>User ID:</strong> {booking.user_id}</div>
        <div><strong>Slot ID:</strong> {booking.slot_id}</div>
        <div><strong>Start:</strong> {booking.start_time}</div>
        <div><strong>Status:</strong> {booking.booking_status}</div>
        <div><strong>Vehicle:</strong> {booking.vehicle_number}</div>
        <div><strong>Total:</strong> {booking.total_amount}</div>
      </div>
    </div>
  )
}
